import previewImg from '@/components/preview-img/index.vue'
export default {
	data() {
		return {
			comment: '',
			imgs: [],
			city: '重庆',
			place: 'xxx',
			pay: false,
			duration: '',
			money: 0,
			switch: false
		}
	},
	components: {
		previewImg
	},
	onLoad(options) {
		uni.setNavigationBarTitle({
			title: options.name
		})
		this.queryId = options.id
		this.queryName = options.name
	},
	methods: {
		chooseImg() {
			uni.chooseImage({
				sourceType: ['album'],
				sizeType: 'original',
				success: (file) => {
					this.imgs = file.tempFilePaths
					console.log(file.tempFiles,88)
				},
				fail(e) {
					console.log(e,222)
				}
			})
		},
		changePay(e) {
			this.pay = !this.pay
			// this.pay = e.detail.value[0] ? true : false
			this.duration = e.detail.value[0] ? 25 : 0
			this.money = e.detail.value[0] ? 19.9 : 0
		},
		switchChange(e) {
			this.switch = e.detail.value
		},
		submit() {
			uni.navigateTo({
				url:`/pages/payInfo/index?id=${this.queryId}&name=${this.queryName}`,
			})
		}
	}
}
